.faq-page {
  
  &__title {
    margin-bottom: .3em;

    @media screen and (min-width: 900px) {
      margin-bottom: 1.2em;
    }
  }
}

.faq {
  @include grid;

  &__content {
    @include grid-item(12);

    @media screen and (min-width: 900px) {
      @include grid-item(9);
      
      order: 2;
    }
  }

  &__sidebar {
    @include grid-item(12);

    @media screen and (min-width: 900px) {
      @include grid-item(3);

      order: 1;
    } 
  }

  &__accordions {
    margin-bottom: 37px;

    @media screen and (min-width: 900px) {
      margin-bottom: 110px;
    }
  }

  &__ask {
    margin-bottom: 49px;

    @media screen and (min-width: 900px) {
      margin-bottom: 120px;
    }
  }

  &__ask-title {
    margin-bottom: .65em;
    padding-right: 73px;

    @media screen and (min-width: 900px) {
      padding-right: 0;
      font-size: 3.2rem;
      line-height: to-em(36px, 3.2rem);
    }
  }
}