.project-preview {
  position: relative;

  &[href] {
    display: block;
    text-decoration: none;
  }
  
  &__img {
    width: 100%;
    margin-bottom: 15px;
    max-height: 513px;
    display: flex;
    object-fit: cover;
  }

  &__tag {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 1;
    
    @media screen and (min-width: 600px) {
      left: 20px;// if min-width: 600px i add margin-left/right -15px 
      top: 20px;
    }
  }

  &__desc {
    margin-bottom: 0;
    font-family: $ff--sec;

    @media screen and (max-width: 599px) {
      font-size: 1.3rem;
      line-height: to-em(19px, 1.3rem);
    }
  }

  &__label {
    position: absolute;
    top: 0;
    right: 0;
    padding: to-em(10px 14px, 1rem);
    font-family: $ff--sec;
    font-size: 1rem;
    line-height: to-em(15px, 1rem);
    color: $txt-c--sec;
    background-color: $c--darken;
  
    @media screen and (min-width: 900px) {
      padding: to-em(19px 43px, 1.5rem);
      font-size: inherit;
    }
  }
}