.manager {
  display: flex;
  flex-flow: column;
  align-items: center;
  overflow: hidden;
  text-align: center;
  text-decoration: none;

  &__pic {
    margin-bottom: 18px;
    width: 72px;
    border-radius: 50%;

    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }
  &__name {
    max-width: 100%;
    span {
      display: block;
      width: 100%;
      @include txt-nowrap;
    }
    margin-bottom: 0;
    padding: 0 .5em;
    font-size: 1.6rem;
    line-height: to-em(18px, 1.6rem);
    font-weight: 600;
    text-align: center;
  }

  &__prof,
  &__email {
    font-family: $ff--sec;
  }

  &__prof {
    margin-bottom: 0;
    font-size: 1.3rem;
    line-height: to-em(23px, 1.3rem);

    @media screen and (min-width: 900px) {
      font-size: inherit;
    }
  }

  &__email {
    margin-top: auto;
  }
}
