.gallery {
  margin-bottom: 35px;
  
  @media screen and (min-width: 600px) {
    margin: 0 -15px;
    display: flex;
    flex-flow: row wrap;
  }

  &__item {
    @media screen and (min-width: 600px) {
      padding: 0 15px;
      width: 50%;
    }

    &:not(:last-child) {
      margin-bottom: 36px;
      
      @media screen and (min-width: 600px) {
        margin-bottom: 54px;
      }
    }
  }

}