.work-projects-page {

  &__title {
    margin-bottom: 11px;
  }
}

.work-projects {
  @include grid;
  @include grid-wrap;
  
  margin-bottom: 50px;

  @media screen and (min-width: 900px) {
    margin-bottom: 65px;
  }

  &__content {
    @include grid-item(12);
    
    margin-bottom: 15px;

    @media screen and (min-width: 600px) {
      @include grid-item(8);
    }

  }

  &__sidebar {
    @include grid-item(12);

    @media screen and (min-width: 600px) {
      @include grid-item(4);
    }
    
    @media screen and (min-width: 900px) {
      padding-left: 45px;

    }
  }

  &__list-item {

    &:not(:last-child) {
      margin-bottom: 28px;

      @media screen and (min-width: 900px) {
        margin-bottom: 50px;
      }
    }
  }

  &__preview-img {
    @include shadow;
    
    margin-bottom: 13px;

    @media screen and (min-width: 900px) {
      margin-bottom: 22px;
    }
  }

  &__preview-desc {
    margin-bottom: 16px;

    @media screen and (min-width: 900px) {
      margin-bottom: 26px;
    }
  }
}

