.progress {
  
  &__line {
    margin-bottom: 10px;
    width: 100%;
    height: 10px;
    background-color: $c--prim-light;

    @media screen and (min-width: 900px) {
      margin-bottom: 15px;
    }
  }

  &__meter {
    @extend .btn--animated;
    
    height: 100%;
    width: 0;
    background-color: $c--prim;
  }

  &__txt {
    min-height: 23px;
    font-size: 1.3rem;
    line-height: 1em;
    font-family: $ff--sec;
    color: $txt-c--colored-prim;
    
    @media screen and (min-width: 900px) {
      font-size: inherit;
    }
  }
}