.work-project-page {

  &__title {
    margin-bottom: to-em(27px, 2.4rem);
    padding-top: to-em(21px, 2.4rem);
    font-size: 2.4rem;
    line-height: to-em(26px, 2.4rem);

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(25px, 3.2rem);
      padding-top: to-em(50px, 3.2rem);
      max-width: percentage(8/12);
      font-size: 3.2rem;
      line-height: to-em(30px, 3.2rem);
    }
  }

  &__gallery {
    padding-bottom: 38px;

    @media screen and (min-width: 900px) {
      padding-bottom: 0;
    }
  }
}

.work-project {
  @extend .work-projects;

  &__content {
    @extend .work-projects__content;
    margin-bottom: 26px;
  }

  &__sidebar {
    @extend .work-projects__sidebar;
  }

  &__slider {
    margin-bottom: 20px;
  }

  &__progress {
    margin-bottom: 30px;

    @media screen and (min-width: 900px) {
      margin-bottom: 55px;
    }
  }

  &__project-preview {
    margin-bottom: 15px;

    @media screen and (min-width: 900px) {
      margin-bottom: 25px;
    }
  }
}

.schedule {
  margin-bottom: 40px;

  @media screen and (min-width: 900px) {
    margin-bottom: 100px;
  }
  &__title {
    margin-bottom: to-em(5px, 2.4rem);

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(13px, 2.4rem);
    }
  }

  &__desc {
    font-family: $ff--sec;
    font-size: 1.3rem;
    line-height: to-em(21px, 1.3rem);

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(20px, 1.5rem);
      font-size: inherit;
    }
  }
  &__list {
    position: relative;
  }

  &__svg {
    position: absolute;
    left: -50px;
  }

  &__list-item {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 20px;

      @media screen and (min-width: 900px) {
        margin-bottom: 30px;
      }
    }
    &::before {/*
      content: '';
      position: absolute;
      left: -25px;
      top: calc(50% - 10px);
      display: block;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      transform: scale(0);
      background-color: red;
    */}

    &.is-visible::before {/*
      animation: grow .5s ease-out forwards .5s;
      
      @keyframes grow {
        0% {
          transform: scale(0);
        }
        50% {
          transform: scale(1.5);
        }
        100% {
          transform: scale(1);
        }
      }
    */}
  }
}

.stage {
  position: relative;
  background-size: 26px 26px;
  background-repeat: no-repeat;
  background-position: top 16px right 15px;
  opacity: 0;
  transition: opacity .4s ease-out;

  @media screen and (min-width: 780px) {
    background-position: top 21px right 15px;
    background-size: 44px 44px;
  }

  &.is-visible {
    opacity: 1;
  }


  &__body {
    padding: 22px 18px 18px;

    @media screen and (min-width: 900px) {
      padding: 27px 20px;      
    }
  }

  &__caption {
    margin-bottom: to-em(14px, 1.8rem);
  }

  &__list {
    @extend %list-reset;

    font-size: 1.3rem;
    font-family: $ff--sec;
    
    @media screen and (min-width: 900px) {
      font-size: inherit;
      line-height: inherit;
    }

    li {
      &:not(:last-child) {
        margin-bottom: to-em(7px, 1.3rem);

        @media screen and (min-width: 900px) {
          margin-bottom: 0px;
        }
      }

      &::before {
        content: '';
        padding-right: 5px;
      }
    }

  }

  &__footer {
    padding: 14px 20px;
    display: flex;
    flex-direction: column;
    font-size: 1.3rem;
    font-family: $ff--sec;
    text-align: center;

    @media screen and (min-width: 780px) {
      flex-direction: row;
      justify-content: space-between;
      align-items: baseline;
    }
    @media screen and (min-width: 900px) {
      padding: 12px 18px;
      font-size: inherit;
      line-height: inherit;
    }
  }
  
  &__footer-txt {
    margin-bottom: to-em(15px, 1.3rem);

    @media screen and (min-width: 780px) {
      margin-bottom: 0;
    }
    &:last-child() {
      margin-bottom: 0;
    }
  }
  
  &__footer-link {
    padding-top: to-em(14px, 1.3rem);
    border-top: 1px solid;
    text-decoration: none;

    @media screen and (min-width: 780px) {
      padding-top: 0;
      border-top: 0;
    }
  }

  &--complate {
    background-image: url('../img/design/sprite.svg#stage-ok');
    background-color: $c--prim-light;
    
    &::before {
      background-color: $c--prim;
    }

    .stage__caption {
      color: $txt-c--colored-prim;
    }

    .stage__footer {
      background-color: darken($c--prim-light, 10%);
      color: $txt-c--colored-prim;
    }
  }

  &--process {
    background-image: url('../img/design/sprite.svg#stage-proces');
    background-color: $c--sec;

    &::before {
      background-color: $c--sec;
    }
    
    .stage__body {
      color: $txt-c--sec;
    }

    .stage__footer {
      background-color: $c--sec-light;
      color: $txt-c--colored-sec;
    }
  }

  &--pending {
    color: $txt-c--colored-sec;
    background-image: url('../img/design/sprite.svg#stage-pending');
    background-color: lighten($c--sec-light, 5%);
    
    &::before {
      background-color: $c--sec-light;
    }

    .stage__footer {
      background-color: $c--sec-light;
    }
  }
}

.report {
  &__title {
    margin-bottom: to-em(7px, 2.4rem);

    @media screen and (min-width: 900px) {
      margin-bottom: to-em(16px, 3.2rem);
    }
  }
}

.report-stage {
  margin-bottom: 13px;
  padding-bottom: 15px;
  border-bottom: 1px solid $c--grey-light;

  @media screen and (min-width: 900px) {
    margin-bottom: 16px;
    padding-bottom: 25px;
  }

  &__title {
    margin-bottom: 0;
  }

  &__header {
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-size: 1.3rem;
    line-height: to-em(19px, 1.3rem);

    @media screen and (min-width: 900px) {
      font-size: inherit;
      line-height: inherit;
    }
  }

  &__message,
  &__date {
    margin-bottom: 0;
    font-family: $ff--sec;
  }

  &__link {
    @extend .link;

  }
}