.about-page {

}

.about {
  @extend .faq;

  &__content {
    @extend .faq__content;
  }

  &__sidebar {
    @extend .faq__sidebar;
  }
  &__txt {
    margin-bottom: 35px;
    font-family: $ff--sec;

    @media screen and (min-width: 900px) {
      margin-bottom: 0;
    }
  }

  &__team {
    margin-bottom: 40px;

    @media screen and (min-width: 900px) {
      margin-bottom: 0;
    }
  }
  &__team-managers {
    margin-left: -15px;
    margin-right: -15px;
    display: flex;
    flex-wrap: wrap;

    @media screen and (max-width: 599px) {
      margin-bottom: 33px;
    }
  }

  &__manager {
    padding-left: 15px;
    padding-right: 15px;
    flex: 0 1 100%;

    @media screen and (min-width: 600px) {
      flex: 0 1 33.333%;
    }

    &:not(:nth-last-child(-n+3)) {
      @media screen and (min-width: 600px) {
        margin-bottom: 44px;
      }
    }
    &:not(:last-child) {
      @media screen and (max-width: 599px) {
        margin-bottom: 33px;
      }
    }

    & .manager__name {
      font-size: 1.8rem;
      line-height: 1em;
      font-weight: 400;

      @media screen and (min-width: 900px) {
        font-size: 2.2rem;

      }
    }
  }

  &__team-btn {
    padding: to-em(15px 20px, 1.3rem);
    width: 100%;
    border: none;
    color: $txt-c--colored-prim;
    background-color: $c--prim-light;
    
    @media screen and (min-width: 600px) {
      display: none;
    }
  }
}